.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgba(109, 109, 109, 0.493);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgb(255, 255, 255);
    align-items: center;
    min-width: 40%;
    max-width: 40%;
    color: black;
    padding: 20px 10px;
}
.recipe h1{
    padding: 10px;
}
.recipe ol li{
    margin-left: 10%;
    padding: 7px;
}
@media only screen and (max-width: 600px) {
    .recipe{
        width: 100%;
        max-width: 100%;
    }
}


.collapse {
    padding: 0 18px;
    overflow: hidden;
  }
  .collapse p{
      padding: 5px 5px;
      padding-bottom: 5px;
  }


