
.navBar{
    display: flex;
    justify-content: center;
    width: 100%;
    position: absolute;
    color: white;
    transition: 0.4s;

}
.navBar ul{
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: space-around;
    padding: 0px ;

}
.change-color{
    background-color: white;
    color: red;
}
.navBar ul li{
    padding: 10px 2px;
    text-align: center;
    font-size: 1.2rem;
    min-height:28px;
    width: 100%;
    border-radius: 4px;

}
.navBar ul li:first-child{
    background-color: rgba(255, 0, 0, 0.568);
    color: white;
}
.navBar span{
    min-width: 45%;
    text-align: left;
    padding: 5px 20px;
    cursor: pointer;
}
.navBar span img{
    height: 30px;
}
.head{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 20px;

}
.head img{
    max-width: 100%;

}


@media only screen and (max-width: 600px) {
    .navBar{
        flex-direction: column;
    }
    .active{
        height: 100%;
        background-color: white;
    }
    
    .navBar ul{
        width: 100%;
        list-style: none;
        display: flex;
        justify-content: space-around;
        padding: 0px ;

    }
        
    .content-none{
        display: none;
    }



    .navBar ul{
        flex-direction: column;
        height: 100%;
        background-color: white;
        color: red;
        position: sticky;
    }
    .navBar .navOff{
        flex-direction: column;
        position: absolute;
        top: 0;
        left: -102%;
    }
    .navBar ul li{
        padding: 10px 2px;
        text-align: center;
        font-size: 1rem;
        min-height:0px;
        width: 100%;
        border-radius: 0px;
    
    }
    span:active .navBar{
        background-color: black;
    }
    .content{
        flex-direction: column;
    }
 
}