.App{
  min-height: 100vh;
  display: flex;
  max-width: 100vw;
  flex-direction: column;
  background: url('./michael-browning-MtqG1lWcUw0-unsplash\(1\).jpg');
  background-size: cover;
  /* background-size:  100vw auto; */
  background-attachment: fixed;
  background-repeat: no-repeat;
  color: white;
  font-family: 'Cuprum', sans-serif;
}
.mainBody{
  min-height: 100vh;
}

@media only screen and (max-width: 600px){
  .App{
    background-size:cover;
    
  }
}
.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: 0.1px solid rgb(95, 95, 95);
  padding: 10px;
  transition: 0.3s;
}
.search-bar:hover{
  border: 1px solid lightcoral;


}
.search-bar:focus{
  box-shadow: 0px 1px 7px rgba(255, 117, 117, 0.822);
  border: 1px solid lightcoral;

}
.search-button{
  background-color: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
  border: 1px solid lightcoral;
  margin-left: 10px;
}

.stats{
  min-height: 100px;
  min-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

/* Footer */
.foot{
  min-height: 20vh;
  background-color: rgba(32, 32, 32, 0.726);
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
}



.foot .credits{
  width: 50%;
  align-items: center;
  display: flex;
  justify-content: center;
}
.foot .faq{
  display: flex;
  padding-top: 10px;
  width: 50%;
  flex-direction: column;
}
.foot .faq .icons{
  display: flex;
  margin: 2px 2px ;
}

.icons h4{
  align-self: center;
}

.spanIcon{
  width: 30px;
  height: 30px;
  display: flex;
  background: url('imgbin_question-mark-icon-png.png');
  background-size: cover;
  margin-left: 12px;
}



@media only screen and (max-width: 600px){
  .foot{
    flex-direction: column;

  }
  .foot .credits{
    padding-bottom: 60px;
    padding-top: 20px;
    width: 90%;
  }
  .foot .faq{
    width: 90%;
  }

}